<template>
<div id="order-time-search-modal-container">
  <!-- layer popup start -->
  <section id="order-time-search-modal" class="layPop layPopBtm" tabindex="0">
    <h2 class="titLay">{{ $t('order_time_search_modal.주문가능_시간조회') }}</h2>
    <div class="layCont">
      <!-- 레이어팝업 컨텐츠 영역 -->
      <div class="lCont" tabindex="0">
        <ul class="optionList">
          <li>
            <span class="tit">{{ $t('order_time_search_modal.출국장소') }}<em v-if="show_comment_place" class="tip01">{{ $t('order_time_search_modal.select_departure_place') }}</em></span>
            <div class="optionCont">
              <ul class="boxCol02 radBox">
                <li class="frmSel">
                  <input type="radio" id="radSel01" name="radSel01" value="0014" v-model="departurePlace">
                  <label for="radSel01">{{ $t('order_time_search_modal.인천공항') }}</label>
                </li>
                <li class="frmSel">
                  <input type="radio" id="radSel02" name="radSel01" value="0001" v-model="departurePlace">
                  <label for="radSel02">{{ $t('order_time_search_modal.김해공항') }}</label>
                </li>
                <li class="frmSel">
                  <input type="radio" id="radSel03" name="radSel01" value="0023" v-model="departurePlace">
                  <label for="radSel03">{{ $t('order_time_search_modal.김포공항') }}</label>
                </li>
                <li class="frmSel">
                  <input type="radio" id="radSel04" name="radSel01" value="0015" v-model="departurePlace">
                  <label for="radSel04">{{ $t('order_time_search_modal.대구공항') }}</label>
                </li>
                <li class="frmSel">
                  <input type="radio" id="radSel05" name="radSel01" value="0020" v-model="departurePlace">
                  <label for="radSel05">{{ $t('order_time_search_modal.부산국제부두') }}</label>
                </li>
                <li class="frmSel">
                  <input type="radio" id="radSel06" name="radSel01" value="0006" v-model="departurePlace">
                  <label for="radSel06">{{ $t('order_time_search_modal.제주공항') }}</label>
                </li>
                <li class="frmSel">
                  <input type="radio" id="radSel07" name="radSel01" value="0034" v-model="departurePlace">
                  <label for="radSel07">{{ $t('order_time_search_modal.청주국제공항') }}</label>
                </li>
                <li class="frmSel">
                  <input type="radio" id="radSel08" name="radSel01" value="0035" v-model="departurePlace">
                  <label for="radSel08">{{ $t('order_time_search_modal.인천1부두') }}</label>
                </li>
                <li class="frmSel">
                  <input type="radio" id="radSel09" name="radSel01" value="0036" v-model="departurePlace">
                  <label for="radSel09">{{ $t('order_time_search_modal.인천2부두') }}</label>
                </li>
              </ul>
            </div>
          </li>
          <li>
            <span class="tit">{{ $t('order_time_search_modal.출국일시') }}<em class="tip02 order-time-search-modal-tooltip">{{ $t('order_time_search_modal.SELECT_DEPARTURE_TIME') }}</em></span>
            <div class="frmInp flexWrap chatPopDt">
              <div class="flexBox">
                <div class="calenWrap" data-header="날짜 선택" @click="clickDateField">
                  <div class="calenInp">
                    <input type="tel" id="inp_flyDate" title="날짜 선택" :placeholder="$t('order_time_search_modal.날짜선택')" data-id="dateFirst" :data-range="dateRange" readonly>
                    <button type="button" class="ui-datepicker-trigger">{{ $t('order_time_search_modal.날짜선택') }}</button>
                  </div>
                </div>
              </div>
              <div class="flexBox">
                <div class="calenWrap" data-header="시간 선택" @click="clickTimeField">
                  <div class="calenInp timeInp">
                    <input type="tel" id="inp_flyTime" title="시간 선택" :placeholder="$t('order_time_search_modal.시간선택')" data-id="dateFirst" readonly>
                    <button type="button" class="ui-datepicker-trigger">{{ $t('order_time_search_modal.시간선택') }}</button>
                  </div>
                </div>
              </div>
            </div>
          </li>
        </ul><!-- //optionList -->
        <div class="btnArea">
          <button type="button" id="order-time-button" class="btnSSG btnM action" @click="onClickOk">{{ $t('order_time_search_modal.확인') }}</button>
        </div>
      </div>
    </div>
    <button type="button" class="closeL" @click="hideModal">{{ $t('order_time_search_modal.닫기') }}</button>
    <button type="button" class="refresh" @click="reset">{{ $t('order_time_search_modal.새로고침') }}</button>
  </section>
  <div class="dimmed" @click="hideModal"></div>
</div>
</template>
<style>
label{
  cursor:pointer;
}

</style>

<script>
import { userChatHandler } from '@/utils/chatbotChatHandler';
import deptCode from '@/dictionary/deptCode';
import $ from 'jquery';

export default {
  name: 'OrderTimeSearchModal',
  data() {
    return {
      dateRange: this.$moment().format('YYYY.MM.DD-'),
      isAddedDateEventListener: false,
      isAddedTimeEventListener: false,
    };
  },
  mounted() {
    if (this.$lang === this.$dictionary.languageType.CN) {
      const style = document.createElement('style');
      style.type = 'text/css';
      style.innerHTML = ".ui-datepicker .ui-datepicker-calendar td.ui-datepicker-today a:after { content: '今天' }";
      document.getElementsByTagName('head')[0].appendChild(style);
    }
  },
  computed: {
    departurePlace: {
      get() {
        return this.$store.state.orderTimeSearchModal_departurePlace;
      },
      set(value) {
        this.$store.commit('SET_SET_ORDERTIME_PLACE', value);
        this.updateButton();
      },
    },
    show_comment_place() {
      return this.$store.state.orderTimeSearchModal_departurePlace === '' || this.$store.state.orderTimeSearchModal_departurePlace === null;
    },
  },
  methods: {
    hideModal(shouldLog) {
      if (shouldLog !== 0) {
        this.$analyticsHandler.apply(this.$dictionary.tagType.CB_BTN_KR_51);
      }
      this.reset(0);
      this.$uiHandler.hideOrderPossibleTimeModal();
    },
    reset(shouldLog) {
      this.departurePlace = null;
      this.$uiHandler.setOrderPossibleTimeModalDate(null);
      this.$uiHandler.setOrderPossibleTimeModalTime(null);
      this.updateButton();
      if (shouldLog !== 0) {
        this.$analyticsHandler.apply(this.$dictionary.tagType.CB_BTN_KR_50);
      }
    },
    parseDate(date, time) {
      const [pivot, t] = time.split(' ');
      const [year, month, day] = date.split('.');
      const [hour, minute] = t.split(':');

      const momentObj = this.$moment()
          .year(year)
          .month(Number(month) - 1)
          .date(day)
          .hour(hour)
          .minute(minute);

      if (pivot.toLocaleUpperCase() === 'PM') {
        return momentObj.add(12, 'h');
      }

      return momentObj;
    },
    updateButton() {
      const date = this.$uiHandler.getOrderPossibleTimeModalDate();
      const time = this.$uiHandler.getOrderPossibleTimeModalTime();
      const disabled = !date || !time || !this.departurePlace;
      this.$uiHandler.setOrderTimeButtonDisable(disabled);
      this.$uiHandler.setOrderTimeTooltipVisible(!date || !time);
    },
    clickDateField() {
      if (this.isAddedDateEventListener) {
        return;
      }

      $('#ui-datepicker').find('.cb-date-picker-ok').bind('click', () => {
        this.updateButton();
      });
      this.isAddedDateEventListener = true;
    },
    clickTimeField() {
      if (this.isAddedTimeEventListener) {
        return;
      }

      $('#ui-timepicker').find('.cb-time-picker-ok').bind('click', () => {
        this.updateButton();
      });
      this.isAddedTimeEventListener = true;
    },
    onClickOk() {
      const date = this.$uiHandler.getOrderPossibleTimeModalDate();
      const time = this.$uiHandler.getOrderPossibleTimeModalTime();

      if (!date || !time || !this.departurePlace) {
        return;
      }

      this.$store.commit('ADD_CONVERSATION', userChatHandler(this.$t('order_time_search_modal.조회하기')));
      const name = deptCode.code2name(this.$lang, this.$store.state.orderTimeSearchModal_departurePlace);
      const dateObj = this.parseDate(date, time);
      this.$store.dispatch('SEND_MESSAGE', {
        message: this.$t('order_time_search_modal.주문가능시간'),
        hidden: true,
        options: {
          depaPlacNm: name,
          exitCd: this.departurePlace,
          exitDt: dateObj.format('YYYYMMDD'),
          exitTime: dateObj.format('HHmm'),
        },
      });
      this.hideModal(0);
      this.isAddedDateEventListener = false;
      this.isAddedTimeEventListener = false;
      this.$analyticsHandler.apply(this.$dictionary.tagType.CB_BTN_KR_52);
    },
  },
};
</script>